.loader {
  width: 100%;
  height: 100vh;
  background-color: black;
  position: fixed;
  z-index: 10;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader svg {
  width: 100%;
}
.loading-text {
  width: 100%;
  font-weight: bold;
  color: #fff;
  transform: translate(0.4rem);
  margin-top: 1rem;
}
